@import '../../commonStyles/colors.scss';

.title{
    font-weight: 600;
    font-size: 24px;
    line-height: 1.2;
    color: var(--title-text-color);
}

.form{
    margin-top: 32px;
    &_label{
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;  
    }
    &_input{

        padding-top: 11px;
        padding-bottom: 11px;

        background-color: #F8F9FC;
        color: var(--placeholder-text-color);
        border: none;
        border-radius: 4px;
        outline: none;
    }
    &_btn{
        width: 100%;
        margin-top: 32px;
        padding-top: 11px;
        padding-bottom: 11px;

        background-color: var(--blue-color);
        border-color: var(--blue-color);
    }
    &_text{
        text-align: center;
        margin-top: 16px;

        font-size: 12px;
        line-height: 1.5;
    }
    &_span{
        color: var(--blue-color);
    }
}
